<template>
    <div>
        <loading :active.sync="isLoading">
            <img src="@/assets/img/loading.gif" alt="" width="200">
        </loading>
        <div class="mt-3 d-flex">
            <button class="btn btn-outline-success btn-sm d-flex align-items-center ml-auto" @click="openModal(true)">
                <i class="material-icons">add</i>
            </button>
        </div>
        <div class="table-responsive">
            <table class="table table-hover mt-3">
                <thead class="table-becare">
                    <tr class="text-nowrap">
                        <th width="100"></th>
                        <th></th>
                        <th width="80"></th>
                        <th width="80"></th>
                        <th width="100"></th>
                        <th width="150"></th>
                    </tr>
                </thead>
                <tbody class="bg-white">
                    <tr v-for="item in products" :key="item.id">
                        <td>
                            <span class="badge" :class="categoryClassName(item.category)">{{item.category}}</span>                
                        </td>
                        <td>{{item.title}}</td>
                        <td class="text-right" v-if="item.origin_price">{{item.origin_price | currency}}</td>
                        <td class="text-right" v-else></td>
                        <td class="text-right" v-if="item.price">{{item.price | currency}}</td>
                        <td class="text-right" v-else></td>
                        <td>
                            <span class="text-success font-weight-bold" v-if="item.is_enabled"></span>
                            <span v-else></span>
                        </td>
                        <td>
                            <button class="btn btn-outline-main btn-sm mb-2 mb-md-0" @click="openModal(false,item)"></button>
                            <button class="btn btn-outline-secondary btn-sm mb-2 mb-md-0" @click="deleteModal(item)"></button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <pagination :page-data="pagination" @changepage="getProducts" class="d-flex justify-content-center"></pagination>
        <modal id="productModal">
            <div slot="modalHeader" class="modal-header bg-main text-white">
                <h5 class="modal-title" id="exampleModalLabel">
                    <span v-if="isNew"></span>
                    <span v-else></span>
                </h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true" class="text-white">&times;</span>
                </button>
            </div>
            <div slot="modalBody" class="modal-body">
                <div class="row">
                    <div class="col-sm-4">
                        <div class="form-group">
                            <label for="image"></label>
                            <input type="text" class="form-control" id="image" v-model="tempProduct.imageUrl" placeholder="">
                        </div>
                        <div class="form-group">
                            <label for="customFile" class="upload-filed rounded" @dragenter="fileDragHover" @dragleave="fileDragHover">
                                <i class="fas fa-cloud-upload-alt mr-1"></i>Click & Upload
                                <i class="fas fa-spinner fa-spin" v-if="status.fileuploading"></i>
                                <input type="file" id="customFile" class="form-control upload-contorl" ref="files" @change="uploadFile">
                            </label>
                        </div>
                        <img class="img-fluid" alt="" :src="tempProduct.imageUrl">
                    </div>
                    <div class="col-sm-8">
                        <div class="form-group">
                            <label for="title">*</label>
                            <input type="text" class="form-control" id="title"  name="title" placeholder=""
                                v-model="tempProduct.title" v-validate="'required'" :class="{'is-invalid': errors.has('title')}">
                            <span class="text-danger" v-if="errors.has('title')"></span>
                        </div>
                        <div class="form-row">
                            <div class="form-group col-md-6">
                                <label for="category"></label>
                                <select name="category" id="category" v-model="tempProduct.category" class="form-control">
                                    <option value="Switch">Switch</option>
                                    <option value="3DS">3DS</option>
                                    <option value="PS4">PS4</option>
                                </select>
                            </div>
                            <div class="form-group col-md-6">
                                <label for="unit">*</label>
                                <input type="unit" class="form-control" id="unit" placeholder="" name="unit"
                                    v-model="tempProduct.unit" v-validate="'required'" :class="{'is-invalid': errors.has('unit')}">
                                <span class="text-danger" v-if="errors.has('unit')"></span>
                            </div>
                        </div>
                        <div class="form-row">
                            <div class="form-group col-md-6">
                                <label for="origin_price">*</label>
                                <input type="number" class="form-control" id="origin_price" placeholder="" name="origin_price"
                                    v-model="tempProduct.origin_price" v-validate="{ required: true, regex: /^([0-9]+)$/ }" 
                                    :class="{'is-invalid': errors.has('origin_price')}">
                                <span class="text-danger" v-if="errors.has('origin_price')"></span>
                            </div>
                            <div class="form-group col-md-6">
                                <label for="price">*</label>
                                <input type="number" class="form-control" id="price" placeholder="" name="price"
                                    v-model="tempProduct.price" v-validate="{ required: true, regex: /^([0-9]+)$/ }"
                                    :class="{'is-invalid': errors.has('price')}">
                                <span class="text-danger" v-if="errors.has('origin_price')"></span>
                            </div>
                        </div>
                        <hr>
                        <div class="form-group">
                            <label for="description"></label>
                            <textarea type="text" class="form-control" id="description" v-model="tempProduct.description" placeholder=""></textarea>
                        </div>
                        <div class="form-group">
                            <label for="content"></label>
                            <textarea type="text" class="form-control" id="content" v-model="tempProduct.content" placeholder=""></textarea>
                        </div>
                        <div class="form-group">
                            <div class="form-check">
                                <input class="form-check-input" type="checkbox" id="is_enabled" v-model="tempProduct.is_enabled" :true-value="1" :false-value="0">
                                <label class="form-check-label" for="is_enabled"></label>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div slot="modalFooter" class="modal-footer">
                <button type="button" class="btn btn-outline-secondary border-0" data-dismiss="modal"></button>
                <button type="button" class="btn btn-becare text-main" @click="updateProduct" :disabled="status.loading">
                    <i class="fas fa-spinner fa-spin mr-1" v-if="status.loading"></i>
                </button>
            </div>
        </modal>
        <modal id="delProductModal" :modal-data="tempProduct" :isloading="status.loading" @doit="delProduct"></modal>
    </div>
</template>


<script>
//JQuery
import $ from 'jquery';
export default {
  data() {
    return {
      products: [],
      pagination: {},
      tempProduct: {},
      isNew: false,
      isLoading: false,
      status: {
        fileuploading: false,
        loading:false,
      },
    }
  },
  methods: {
    //es6  , getProducts()
    getProducts(page = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        //console.log('API', response.data.success);
        if (response.data.success) {
          vm.products = response.data.products;
          //console.log(response.data);
          vm.isLoading = false;
          vm.pagination = response.data.pagination;
        } else {
          vm.$bus.$emit('message:push', response.data.message, 'danger');
          setTimeout(() => {
            $('#productModal').modal('hide');
            $('#delProductModal').modal('hide');
            vm.$router.push('/login');
          }, 1500)
        }
      })
    },
    openModal(isNew, item) {
      this.$validator.reset();
      if (isNew) {
        this.tempProduct = {
            category:'Switch',
        };
        this.isNew = true;
      } else {
        // 
        //
        //   this.tempProduct = {
        //       ...item
        //   };
        // item - ES6 
        this.tempProduct = Object.assign({}, item);
        this.isNew = false;
      }
      $('#productModal').modal('show');
      $('#customFile').val('');
    },
    deleteModal(item) {
      //  tempProduct
      // this.tempProduct = {
      //     ...item
      // };
      this.tempProduct = Object.assign({}, item);
      $('#delProductModal').modal('show');
    },
    updateProduct() {
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
      const vm = this;
      let httpMethod = 'post';
      if (!vm.isNew) {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        httpMethod = 'put';
      }  
      //  API
      //console.log({data: vm.tempProduct});
      this.$validator.validate().then((result) => { 
          if (result) {
            vm.status.loading = true;
            this.$http[httpMethod](api, { data: vm.tempProduct }).then(response => {
                //vm.products = response.data.products;
                //console.log('API', response.data.success)
                if (response.data.success) {
                    $('#productModal').modal('hide');
                    vm.status.loading = false;
                    vm.getProducts(vm.pagination.current_page);
                    vm.$bus.$emit('message:push', response.data.message, 'success');
                } else {
                    $('#productModal').modal('hide');
                    vm.status.loading = false;            
                    vm.$bus.$emit('message:push', response.data.message, 'danger');
                }
            })
          }else{
            vm.$bus.$emit('message:push', ``, 'danger');
          }
      });
    },
    delProduct() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
      vm.status.loading = true;
      this.$http.delete(api).then(response => {
        //console.log('API', response.data.success);
        if (response.data.success) {
          $('#delProductModal').modal('hide');
          vm.status.loading = false; 
          vm.$bus.$emit('message:push', response.data.message, 'success');
          //console.log('');
          vm.getProducts();
        } else {
          $('#delProductModal').modal('hide');
          vm.status.loading = false;     
          vm.$bus.$emit('message:push', response.data.message, 'danger');
        }
      })
    },
    uploadFile() {
      //console.log(this)
      if (this.$refs.files.files.length == '0') {return}
      const uploadedFile = this.$refs.files.files[0];
      const uploadfield = document.querySelector('.upload-filed');
      const vm = this;
      //FormData 
      const formData = new FormData();
      formData.append('file-to-upload', uploadedFile);
      vm.status.fileuploading = true;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
      this.$http.post(url, formData, {
          headers: {
            //headers
            'Content-Type': 'multipart/form-data'
          }
        })
        .then(response => {
          //console.log('API', response.data.success)
          vm.status.fileuploading = false
          if (response.data.success) {
            // - Getter . Setter
            // vm.tempProduct.imageUrl = response.data.imageUrl;
            // console.log(vm.tempProduct);
            //
            vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl);
            uploadfield.className = "upload-filed";
          } else {
            uploadfield.className = "upload-filed";
            $('#productModal').modal('hide');
            vm.$bus.$emit('message:push', response.data.message, 'danger');
          }
        })
    },
    fileDragHover(e){
        const uploadfield = document.querySelector('.upload-filed');
        e.type == "dragenter" ? uploadfield.className = "upload-filed dragin" : uploadfield.className = "upload-filed";
    },
    categoryClassName(category){
        let className = '';
        switch (category) {
            case 'Switch':
                return className = 'badge-danger'
                break;     
            case '3DS':
                return className = 'badge-main'
                break;
            case 'PS4':
                return className = 'badge-dark'
                break;
        }
    },
  },
  created() {
    this.getProducts();
  }
}
</script>


