<template>
    <div>
        <loading :active.sync="isLoading">
            <img src="@/assets/img/loading.gif" alt="" width="200">
        </loading>
        <div class="table-responsive mt-5">
            <table class="table table-hover mt-4">
                <thead class="table-becare">
                    <tr class="text-nowrap">
                        <th width="100"></th>
                        <th width="250"></th>
                        <th></th>
                        <th></th>
                        <th width="100"></th>
                        <th width="160"></th>
                        <th width="80"></th>
                    </tr>
                </thead>
                <tbody class="bg-white">
                    <tr v-for="item in orders" :key="item.id" v-if="!item.is_delete">
                        <td>{{item.create_at | timeTamps}}</td>
                        <td>
                            <ul>
                                <li>ID<br>{{item.id}}
                                <li>{{item.user.name}}</li>
                                <li>{{item.user.email}}</li>
                                <li>{{item.user.address}}</li>
                                <li>{{item.user.tel}}</li>
                            </ul>
                        </td>
                        <td>
                            <ul>
                                <li v-for="prod in item.products" :key="prod.id" class="mb-2">
                                    <div class="text-nowrap"> 
                                        {{prod.product.title}}<br>
                                        {{prod.qty}} {{prod.product.unit}} - 
                                        <span class="text-main" v-if="prod.final_total >= prod.total">{{prod.final_total | currency}}</span>
                                        <span class="text-danger" v-if="prod.final_total < prod.total">{{prod.final_total | currency}} ({{prod.coupon.title}})</span>
                                    </div>
                                </li>
                            </ul>
                        </td>
                        <td>
                            {{item.message}}
                        </td>
                        <td class="text-right">{{item.total | currency}}</td>
                        <td>
                            <ul v-if="item.is_paid">
                                <li class="text-success font-weight-bold"></li>
                                <li>{{item.paid_date | timeTamps}}</li>
                            </ul>
                            <span v-else></span>
                        </td>
                        <td>
                            <button class="btn btn-outline-main btn-block" @click="editModal(item)"></button>
                            <button class="btn btn-outline-secondary btn-block" v-if="!item.is_paid" @click="delModal(item)"></button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <pagination :page-data="pagination" @changepage="getOrders" class="d-flex justify-content-center"></pagination>    
        <modal id="editOrderModal">
            <div slot="modalHeader" class="modal-header bg-main text-white">
                <h5 class="modal-title" id="exampleModalLabel">
                    <span></span>
                </h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true" class="text-white">&times;</span>
                </button>
            </div>
            <div slot="modalBody" class="modal-body">
                <nav>
                    <div class="nav nav-tabs" id="nav-tab" role="tablist">
                        <a class="nav-item nav-link font-weight-bold active" id="nav-orderinfo-tab" data-toggle="tab" href="#nav-orderinfo" role="tab" aria-controls="nav-orderinfo" aria-selected="true"></a>
                        <a class="nav-item nav-link font-weight-bold " id="nav-prodlist-tab" data-toggle="tab" href="#nav-prodlist" role="tab" aria-controls="nav-prodlist" aria-selected="false"></a>
                    </div>
                </nav>
                <div class="tab-content" id="nav-tabContent">
                    <div class="tab-pane fade show active pt-3" id="nav-orderinfo" role="tabpanel" aria-labelledby="nav-orderinfo-tab">
                        <div class="row">
                            <div class="col-md-12">
                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                        <label for="orderName">*</label>
                                        <input type="text" class="form-control" id="orderName" name="Name" v-model="tempOrder.user.name" placeholder=""
                                            v-validate="'required'" :class="{'is-invalid': errors.has('Name')}">
                                        <span class="text-danger" v-if="errors.has('Name')"></span>
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="orderEmail">*</label>
                                        <input type="email" class="form-control" :class="{'is-invalid': errors.has('email')}" name="email" id="orderEmail"
                                            v-model="tempOrder.user.email" placeholder=" Email" v-validate="'required|email'" >
                                        <span class="text-danger" v-if="errors.has('email')">{{errors.first('email')}}</span>
                                    </div>
                                </div>
                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                        <label for="orderAddress">*</label>
                                        <input type="text" class="form-control" id="orderAddress" name="address" :class="{'is-invalid': errors.has('address')}"
                                            v-model="tempOrder.user.address" placeholder=""  v-validate="'required'">
                                        <span class="text-danger" v-if="errors.has('address')"></span>
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="orderTel">*</label>
                                        <input type="tel" class="form-control" id="orderTel" name="regex" v-model="tempOrder.user.tel" 
                                            v-validate="{ required: true, regex: /^([0-9]+)$/ }" placeholder="" :class="{'is-invalid': errors.has('regex')}">
                                        <span class="text-danger" v-if="errors.has('regex')"></span>
                                    </div>
                                </div>
                                <div class="form-group">
                                    <label for="textarea"></label>
                                    <textarea class="form-control" id="textarea" rows="3" v-model="tempOrder.message" placeholder=""></textarea>
                                </div>
                                <div class="form-group">
                                    <div class="form-check">
                                        <input class="form-check-input" type="checkbox" id="is_paid" v-model="tempOrder.is_paid">
                                        <label class="form-check-label" for="is_paid"></label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="tab-pane fade pt-3" id="nav-prodlist" role="tabpanel" aria-labelledby="nav-prodlist-tab">
                        <div class="row">
                            <div class="col-md-12">
                                <div class="form-row" v-for="(item,key, index) in tempOrder.products" :key="item.id">
                                    <div class="form-group col-md-9">
                                        <div class="input-group">
                                            <div class="input-group-prepend">
                                                <span class="input-group-text bg-main text-white" id="basic-addon1"> {{index +1}}</span>
                                            </div>
                                            <input type="text" class="form-control"  aria-label="prodName" style="cursor:default;" readonly
                                                v-model="item.product.title">
                                        </div>
                                        <p v-if="item.coupon" class="text-danger font-weight-bold mb-0">
                                            <span>*{{item.coupon.title}}  - </span>
                                            <span>{{(item.product.price * item.coupon.percent) / 100 | currency}} 1/</span>
                                        </p>
                                        <p v-else class="text-dontcare mb-0">
                                            <span > - </span>
                                            <span>{{item.product.price | currency}} 1/{{item.product.unit}}</span>
                                        </p>
                                    </div>
                                    <div class="form-group col-md-3">
                                        <div class="input-group">
                                            <div class="input-group-prepend">
                                                <span class="input-group-text bg-main text-white" id="basic-addon1">*</span>
                                            </div>
                                            <input type="number" class="form-control"  aria-label="num"  v-model="item.qty" :name="`num${index}`"
                                                v-validate="{ required: true, regex: /^([0-9]+)$/ }" placeholder="" :class="{'is-invalid': errors.has('num' + index)}">
                                        </div>
                                        <p class="mb-0  text-dontcare text-right">
                                            <span v-if="item.coupon"> {{((item.product.price * item.coupon.percent) / 100) * item.qty | currency}}</span>
                                            <span v-else> {{(item.product.price * item.qty) | currency}}</span>
                                        </p>
                                    </div>
                                </div>
                                <div class="text-right text-danger font-weight-bold"> {{calcFinalTotal | currency}}</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div slot="modalFooter" class="modal-footer">
                <button type="button" class="btn btn-outline-secondary border-0" data-dismiss="modal"></button>
                <button type="button" class="btn btn-becare text-main" @click="updateOrder" :disabled="status.loading">
                    <i class="fas fa-spinner fa-spin mr-1" v-if="status.loading"></i>
                </button>
            </div>
        </modal>
        <modal id="delOrderModal" :isloading="status.loading" @doit="deleteOrder">
            <div slot="modalHeader" class="modal-header bg-danger text-white">
                <h5 class="modal-title" id="exampleModalLabel">
                    <span></span>
                </h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true" class="text-white">&times;</span>
                </button>
            </div>
            <div slot="modalBody" class="modal-body">
                <div><span class="text-danger"></span>?</div>      
                <ul v-if="tempOrder.user" class="list-group my-3">
                    <li class="list-group-item list-group-item-warning"> ID{{tempOrder.id}}</li>
                    <li class="list-group-item list-group-item-light">{{tempOrder.create_at | timeTamps}}</li>
                    <li class="list-group-item list-group-item-light">{{tempOrder.user.name}}</li>
                    <li class="list-group-item list-group-item-light">{{tempOrder.user.email}}</li>
                    <li class="list-group-item list-group-item-light">{{tempOrder.user.address}}</li>
                    <li class="list-group-item list-group-item-light">{{tempOrder.user.tel}}</li>
                    <li class="list-group-item list-group-item-light">
                        <span class="font-weight-bold text-main">-  -</span>
                        <div v-for="prod in tempOrder.products" :key="prod.id">
                            {{prod.product.title}} * {{prod.qty}}
                        </div>        
                    </li>
                    <li class="list-group-item list-group-item-light text-right text-main font-weight-bold">
                         {{tempOrder.total | currency}}
                    </li>
                </ul>
            </div>
        </modal>
    </div>
</template>


<script>
import $ from 'jquery';
export default {
  data() {
    return {
      orders: [],
      pagination: {},
      isLoading: false,
      tempOrder:{
          user:{},
          products:{},
      },
      status:{
        loading:false,
      },
    }
  },
  methods: {
    editModal(item) {
      //
      //this.tempOrder = Object.assign({}, item);
      //JSON.stringify,JSON.parse
      this.tempOrder = JSON.parse(JSON.stringify(item));
      this.$validator.reset();
      $('#editOrderModal').modal('show');
    },
    delModal(item){
      this.tempOrder = Object.assign({}, item);
      $('#delOrderModal').modal('show');
    },
    getOrders(page = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/orders?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
          vm.orders = response.data.orders;
          vm.pagination = response.data.pagination;
          vm.isLoading = false;
      })
    },
    deleteOrder(){
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/order/${vm.tempOrder.id}`;
      vm.status.loading = true;
      vm.tempOrder.is_delete = true;
      this.$http.put(api,{data:vm.tempOrder}).then(response => {
        if (response.data.success) {
            $('#delOrderModal').modal('hide');
            vm.status.loading = false;
            vm.getOrders(vm.pagination.current_page);
            vm.$bus.$emit('message:push', response.data.message, 'success');
        } 
      })
    },
    updateOrder(){
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/order/${vm.tempOrder.id}`;
      this.$validator.validate().then((result) => { 
          if (result) {
            vm.status.loading = true;
            //
            if(vm.tempOrder.is_paid){
                const nowDate = new Date();
                vm.tempOrder.paid_date = Math.floor(nowDate / 1000);
            } else {
                vm.tempOrder.paid_date = '';
            }
            let allFinalTotal = 0;
            let objectLength = Object.keys(vm.tempOrder.products).length;
            for (let i = 0 ; i<objectLength;i++){
                //console.log(i);
                let prodID = Object.keys(vm.tempOrder.products)[i];
                let prodObj =  vm.tempOrder.products;
                //console.log(vm.tempOrder.products[prodID].product_id);
                if(prodObj[prodID].coupon){
                    //
                    prodObj[prodID].final_total = ((prodObj[prodID].qty * prodObj[prodID].product.price) * prodObj[prodID].coupon.percent) / 100;
                    //
                    prodObj[prodID].total = prodObj[prodID].qty * prodObj[prodID].product.price;
                }else{ 
                    prodObj[prodID].total = prodObj[prodID].qty * prodObj[prodID].product.price;
                    prodObj[prodID].final_total = prodObj[prodID].total;
                }
                allFinalTotal = allFinalTotal + prodObj[prodID].final_total;
            }
            vm.tempOrder.total = allFinalTotal;
            //console.log(vm.tempOrder);
            this.$http.put(api,{data:vm.tempOrder}).then(response => {
                if (response.data.success) {
                    $('#editOrderModal').modal('hide');
                    vm.status.loading = false;
                    vm.getOrders(vm.pagination.current_page);
                    vm.$bus.$emit('message:push', response.data.message, 'success');
                } 
            })
          } else {
            vm.$bus.$emit('message:push', ``, 'danger');
          }
      });


    }
  },
  created() {
    this.getOrders();
  },
  computed:{
      calcFinalTotal(){
        const vm = this;
        let allFinalTotal = 0;
        let objectLength = Object.keys(vm.tempOrder.products).length;
        for (let i = 0 ; i<objectLength;i++){
            //console.log(i);
            let prodID = Object.keys(vm.tempOrder.products)[i];
            let prodObj =  vm.tempOrder.products;
            //console.log(vm.tempOrder.products[prodID].product_id);
            if(prodObj[prodID].coupon){
                //
                prodObj[prodID].final_total = ((prodObj[prodID].qty * prodObj[prodID].product.price) * prodObj[prodID].coupon.percent) / 100;
                //
                prodObj[prodID].total = prodObj[prodID].qty * prodObj[prodID].product.price;
            }else{ 
                prodObj[prodID].total = prodObj[prodID].qty * prodObj[prodID].product.price;
                prodObj[prodID].final_total = prodObj[prodID].total;
            }
            allFinalTotal = allFinalTotal + prodObj[prodID].final_total;
        }
        return allFinalTotal;
        //console.log(vm.tempOrder);
      }
  }
}
</script>


